<template>
  <div v-bind:id="id" class="container">
    <picture >
      <source srcset="@/assets/eaelogo-small.png" media="(max-width: 400px)">
      <source srcset="@/assets/eaelogo.png">
      <img id="image" src="@/assets/eaelogo.png" alt="">
      <p>EAEAT</p>
    </picture>
  </div>
</template>

<script>
export default {
  name: 'ResponsiveLogoEAE',
  props: ['id']
}
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#image {
  width: 100%;
  height: auto;
  margin: 0;
  user-select: none;
}
p {
  color: #B5BEC9;
  margin: 0;
  text-align: center;
  letter-spacing: 3px;
  font-size: 0.8em;
}
</style>



