<script>
import firebase from "firebase/app";
import "firebase/database";
import "firebase/storage";
import Schedule from "@/components/Schedule";

function getData(self, uid) {
  firebase
    .database()
    .ref("/regers/" + uid)
    .on(
      "value",
      snapshot => {
        if (snapshot.val()) {
          let capturedData = snapshot.val();
          self.profileData = capturedData;
          self.name = capturedData.name || "";
          self.email = capturedData.email || "";
          self.mobile = capturedData.mobile || "";
          self.academic_id = capturedData.academic_id || "";
          self.englishLevel = capturedData.englishLevel || "";
          self.softwareSkills = capturedData.softwareSkills || [];
          self.otherSkills = capturedData.other || "";
          // self.age = capturedData.age || "";
          // self.facebook = capturedData.facebook || "";
          // self.linkedin = capturedData.linkedin || "";
          // self.level = capturedData.level || "";
          // self.major = capturedData.major || "";
          // self.artMember = capturedData.artMember || "";
          self.question1 = capturedData.question1 || "";
          // self.question2 = capturedData.question2 || "";
          // self.videoLink = capturedData.videoLink || "";
          // self.question3 = capturedData.question3 || "";
        }

        self.$store.dispatch("changeLoading", false);
      },
      e => {
        alert(
          "Couldn't access the database.\nPlease contact one of the leaders."
        );
        self.$store.dispatch("changeLoading", false);
        console.log(e);
      }
    );
}

function uploadCV(store, uid, file, filename) {
  var storageRef = firebase.storage().ref();
  var uploadTask = storageRef
    .child(`ever-pdfs/${uid || firebase.auth().currentUser.uid}/` + filename)
    .put(file);

  uploadTask.on(
    firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
    function(snapshot) {
      var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
      // console.log('Upload is ' + progress + '% done');
      store.dispatch("changeCVStatus", "Upload is " + progress + "% done");
    },
    function(error) {
      // A full list of error codes is available at
      // https://firebase.google.com/docs/storage/web/handle-errors
      switch (error.code) {
        case "storage/unauthorized":
          // User doesn't have permission to access the object
          store.dispatch("changeCVStatus", "Unauthorized.");
          break;

        case "storage/canceled":
          store.dispatch("changeCVStatus", "Canceled.");
          break;

        case "storage/unknown":
          store.dispatch("changeCVStatus", "Unknown error ouccured.");
          break;
      }
    },
    function() {
      // Upload completed successfully, now we can get the download URL
      uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
        // console.log('File available at', downloadURL);
        store.dispatch("changeCVStatus", "CV uploaded.");
      });
    }
  );
}

export default {
  name: "Home",
  data() {
    return {
      profileData: {},
      uid: "",
      name: "",
      email: "",
      mobile: "",
      academic_id: "",
      englishLevel: "Good",
      age: 16,
      facebook: "",
      linkedin: "",
      level: "1",
      softwareSkills: [],
      otherSkills: "",
      major: "Communications",
      artMember: "I am not in ART",
      levelList: ["1", "2", "3"],
      majorList: ["Communications", "Mechatronics", "Chemical"],
      engLevelList: ["Fluent", "Excellent", "Good", "Acceptable"],
      softwareSkillsList: ["Photoshop", "Illustrator", "Premiere"],
      artTeams: [
        "I am not in ART",
        "Shell Eco-Marathon",
        "MATE Arab Regional ROV",
        "Shell Imagine the Future"
      ],
      question1: "",
      questions1_indicator_color: false,
      question2: "",
      questions2_indicator_color: false,
      question3: "",
      questions3_indicator_color: false,
      alert: "",
      file: "",
      filename: "",
      videoLink: ""
    };
  },
  created() {
    // Please turn on (true) loadingScreen in store.js to make this work
    var currentUser = firebase.auth().currentUser;
    if (currentUser != null) {
      this.uid = currentUser.uid;
      getData(this, this.uid);
    }
  },
  computed: {
    questions1_indicator(e) {
      return this.question1.length;
    },
    questions1_indicator_text(e) {
      return `Length: ${this.question1.length}/280`;
    },
    questions2_indicator(e) {
      return this.question2.length;
    },
    questions2_indicator_text(e) {
      return `Length: ${this.question2.length}/280`;
    },
    questions3_indicator(e) {
      return this.question3.length;
    },
    questions3_indicator_text(e) {
      return `Length: ${this.question3.length}/280`;
    },
    cvProgress() {
      return this.$store.state.cvProgress;
    }
  },
  methods: {
    submit(e) {
      if (
        !this.question1.trim() ||
        this.questions1_indicator > 280 ||
        // (!this.question2.trim() || this.questions2_indicator > 280) ||
        // (!this.question3.trim() || this.questions3_indicator > 280) ||
        // (!this.file || !this.filename) ||
        !this.uid ||
        this.uid.length != 28 ||
        !this.name ||
        // !this.facebook ||
        !this.email ||
        !this.mobile ||
        !this.softwareSkills ||
        !this.academic_id ||
        this.academic_id.length != 7 ||
        !this.academic_id.startsWith("2") ||
        // (!this.age || this.age < 15 || this.age > 25) ||
        !this.englishLevel ||
        !this.engLevelList.includes(this.englishLevel)
        // (!this.level || !this.levelList.includes(this.level)) ||
        // (!this.artMember || !this.artTeams.includes(this.artMember)) ||
        // (!this.major || !this.majorList.includes(this.major)
      ) {
        if (!this.uid || this.uid.length != 28) {
          alert(
            "ID is invalid. Don't use your academic ID or custom ID, use the ID given from this page."
          );
        } else {
          alert("Invalid input, please recheck your inputs.");
        }
      } else {
        var self = this;

        firebase
          .database()
          .ref("/regers/" + self.uid || firebase.auth().currentUser.uid)
          .set(
            {
              name: self.name,
              email: self.email,
              mobile: self.mobile,
              academic_id: self.academic_id,
              englishLevel: self.englishLevel,
              softwareSkills: self.softwareSkills,
              other: self.otherSkills,
              // age: self.age,
              // facebook: self.facebook,
              // linkedin: self.linkedin,
              // level: self.level,
              // major: self.major,
              question1: self.question1
              // question2: self.question2,
              // videoLink: self.videoLink,
              // artMember: self.artMember
              // question3: self.question3
            },
            e => {
              if (e) {
                alert(
                  "Couldn't submit the data, please contact one of the leaders."
                );
                console.log(e);
              } else {
                self.alert =
                  "Your information has been saved, keep the current session ID with you to check for new updates or edit your data later.";
              }
            }
          );

        if (this.file || this.filename) {
          uploadCV(this.$store, this.uid, this.file, this.filename);
        }
      }
      e.preventDefault();
    },
    processFile(event) {
      let file = event.target.files[0];
      let filename = file.name;
      let type = file.type;

      if (type.trim().toLowerCase() != "application/pdf") {
        alert("The file must be a PDF file.");
        event.target.value = "";
        return;
      }

      if (file.size > 400000) {
        alert("Maximum file size is 400KB.");
        event.target.value = "";
        return;
      }

      this.file = file;
      this.filename = filename;
    }
  },
  watch: {
    uid() {
      if (this.uid.length == 28) {
        getData(this, this.uid);
      }
    },
    questions1_indicator(e) {
      if (e > 250) {
        this.questions1_indicator_color = true;
      } else {
        this.questions1_indicator_color = false;
      }
    },
    questions2_indicator(e) {
      if (e > 250) {
        this.questions2_indicator_color = true;
      } else {
        this.questions2_indicator_color = false;
      }
    },
    questions3_indicator(e) {
      if (e > 250) {
        this.questions3_indicator_color = true;
      } else {
        this.questions3_indicator_color = false;
      }
    }
  }
};
</script>

