<template>
  <transition name="fade">
    <div id="loadingScreen" v-if="loadingScreen">
      <ResponsiveLogo id="logo"/>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'LoadingScreen',
  data () {
    return {

    }
  },
  computed: {
    loadingScreen() {
      return this.$store.state.loadingScreen
    }
  }
}
</script>

<style scoped>
#logo {
  margin: 0;
  filter: saturate(0);
  opacity: 1;
  user-select: none;
}

#loadingScreen {
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(to bottom, rgb(39, 39, 39), #0E141D);
  top: 0;
  left: 0;
  position: fixed;
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
  padding: 30px 20px;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .3s ease-out;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

</style>



