import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    'loadingScreen': false,
    'cvProgress': ''
  },
  mutations: {
    changeLoading (state, value) {
      state.loadingScreen = value
    },
    changeCVStatus (state, value) {
      state.cvProgress = value
    },
  },
  actions: {
    changeLoading (context, value) {
      context.commit('changeLoading', value)
    },
    changeCVStatus (context, value) {
      context.commit('changeCVStatus', value)
    }
  }
});



