import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
// import NoSessionsNow from '@/components/NoSessionsNow'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      name: 'Home',
      component: Home
    },
  ],
  mode: 'history'
})



