<template>
  <div class="container">
    <h1>There're no events right now, sign up for the upcoming events and we will notify you!</h1>

    <form @submit.prevent="submit">
      <input required type="email" v-model="email" />
      <button type="submit">Sign up</button>
      <p class="alert" v-text="alert" v-if="alert"></p>
    </form>
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/database";
import "firebase/storage";

export default {
  name: "NoSessionsNow",
  data() {
    return {
      email: "",
      alert: ""
    };
  },
  created() {
    // this.$store.dispatch("changeLoading", false);
  },
  methods: {
    submit(e) {
      if (this.email) {
        this.alert = "Hold on...";

        var self = this;

        firebase
          .database()
          .ref("/subs/" + `${self.email.split(".")[0]}`)
          .set(
            {
              email: self.email
            },
            e => {
              if (e) {
                self.alert =
                  "Couldn't submit the data, please check your connection then contact us at support@arrowteam.net.";
                // console.log(e);
              } else {
                self.alert = "Done 🥳.";
              }
            }
          );
      }
      e.preventDefault();
    }
  }
};
</script>

<style scoped>
h1 {
  color: hsl(0, 0%, 90%);
  font-size: 0.8em;
}

form {
  margin-top: 30px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

input,
button,
select,
option,
textarea {
  font-size: 18px;
  padding: 5px 10px;
  margin-bottom: 10px;
  border-radius: 2px;
  outline: none;
  border: none;
  height: 40px;
  background: linear-gradient(-20deg, rgb(17, 17, 17), rgb(37, 37, 37));
  color: rgb(223, 221, 221);
  -webkit-appearance: none;
  background-size: 100% 100%;
  width: 70%;
  transition: background-size 0.2s ease;
}

option {
  color: black;
}

input:focus,
select:focus,
option:focus,
textarea:focus {
  background-size: 400% 400%;
  transition: background-size 0.2s ease;
}

button {
  margin-top: 10px;
  background-image: none;
  background-color: #fb8c00;
  font-weight: bold;
  color: #0e141d;
  transition-duration: 0.1s;
  transition-timing-function: ease-out;
}

button:hover {
  background-color: #ffa726;
}

button:active {
  background-color: #f57c00;
}

textarea {
  padding: 10px;
  height: 80px;
  resize: none;
}

input,
textarea,
select,
option {
  outline: 0px solid #ff9d46;
  transition: 0.1s ease-in;
}

input:focus,
textarea:focus,
select:focus,
option:focus {
  outline: 1px solid #ff9d46;
}

.alert {
  color: white;
  font-weight: bold;
  font-size: 0.8em;
}

@media screen and (min-width: 413px) {
  .container {
    width: 350px;
  }

  input,
  button {
    width: 100%;
  }
}

@media screen and (min-width: 700px) {
  .container {
    width: 670px;
  }
  input,
  button {
    width: 70%;
  }
}
</style>



