<template>
  <picture v-bind:id="id">
    <img v-bind:id="id" src="@/assets/logo.svg" alt>
  </picture>
</template>

<script>
export default {
  name: "ResponsiveLogo",
  props: ["id"]
};
</script>

<style>
img {
  user-select: none;
}
</style>



